.header {
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 80px;
    background-color:rgb(18, 109, 18);
    position: fixed;
    
    .header-wrapper {
        width: 80%;
        height: 100%;
        background-color: green;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        img {
            width: 100px;
        }
        ul {
            width: 500px;
            height: 100%;
            display: flex;
            li {
                text-transform: scale(1.05);
                // padding-top: 19px;
                width: 100%;
                height: 40px;
                text-align: center;
                font-family:'Dhurjati';
                button {
                    border: none;
                    background-color: inherit;
                    font-size: 34px;
                    font-family:'Dhurjati';
                    &:hover {
                        transform: scale(1.05);
                        cursor: pointer;
                        text-decoration: underline;
                    }
                }
                
            }
        }
    }
    
}

@media  screen and (max-width:576px) {
    
    .header {
        .header-wrapper {
            width: 350px;
            ul {
                display: none;
            }
        }
    }
}
@media screen and (min-width:577px) and (max-width:768px) {
    .header {
        .header-wrapper {
            ul {
                li {
                    button {
                        font-size: 32px;
                    }
                }
            }
        }
    }
}